<template>
    <el-row>
      <el-col :span="6">
        <el-form ref="form" :model="form" label-width="150px">
            <el-form-item label="平均月經周期(天)">
              <el-input-number v-model="averagecyc" :min="1" :max="30"></el-input-number>
            </el-form-item>
            <el-form-item label="平均行經期(天)">
              <el-input-number v-model="averagemenses" :min="1" :max="10"></el-input-number>
            </el-form-item>
            <el-form-item label="上次月經開始時間">
               <el-date-picker
                v-model="mcday"
                type="date"
                placeholder="日期">
              </el-date-picker>
            </el-form-item>
        </el-form>
      </el-col>
    </el-row>
</template>

<script>
export default {
   name: "SafePeriod",
   data() {
    return {
        averagecyc:28,
        averagemenses:5,
        mcday:''
    };
  }
}
</script>




