<template>
 <div>
    <el-collapse v-model="activeNames" @change="handleChange" accordion>
      <el-collapse-item v-for="(item,index) in CommandList" :key="index" :name="index">
        <template slot="title">
            <el-alert
              type="primary"
              effect="dark"
              :closable="false"
              >
                <template slot="title">
                    <p style="font-size: 30px;font-family:'微软雅黑';">{{item.name}}</p>
                </template>
            </el-alert>
        </template>
        <div style="font-size: 50px;font-family:'微软雅黑';">{{item.desc}}</div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
export default {
  name: "DiscordBOT",
  data() {
    return {
      CommandList:[
        {name:'+[Command]',desc:'呼叫機器人回答',visable:true},
        {name:'![ParentItem] [ChildItem]',desc:'新增回答語句',visable:false},
        {name:'-[ParentItem] [ChildItem]',desc:'刪除回答語句',visable:true},
        {name:'*[Command]',desc:'列出所有機器人回答',visable:true},
        {name:'+[Command]',desc:'呼叫機器人回答',visable:true},
        {name:'#[Setting]',desc:'各項設定',visable:true},
        {name:'%[ParentItem] [Index] [NewWeight]',desc:'設定權重',visable:true},
      ],
      activeNames: ['1']
    }
  },
  methods: {
    handleChange(){

    }
  },
  mounted(){

  }
}
</script>

<style>
.el-collapse-item__header{
  height:100px;
}
</style>




