<template>
  <el-row :gutter="20">
    <el-col :span="8"></el-col>
    <el-col :span="8">
      <el-table
        :data="tableData.filter(data => !search || data.Title.toLowerCase().includes(search.toLowerCase()))"
        border
        style="width: 100%"
        height="500"
        v-loading="loading"
        size="medium">
        <el-table-column
          v-for="(item,index) in tableCoulumn"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          :width="item.width"
          sortable
        ></el-table-column>
        <el-table-column align="right">
          <template slot="header" slot-scope="scope">
            <el-input v-model="search" size="mini" placeholder="輸入幣別搜尋" />
          </template>
        </el-table-column>
      </el-table>
      <p>資料來源: https://tw.rter.info/howto_currencyapi.php</p>
    </el-col>
    <el-col :span="8"></el-col>
  </el-row>
</template>

<script>
import axios from "axios";
export default {
  name: "ExchangeRate",
  data() {
    return {
      tableData: [],
      tableCoulumn: [
        { prop: "Title", label: "幣別", width: "150" },
        { prop: "Exrate", label: "台幣", width: "150" },
        { prop: "UTC", label: "時間", width: "200" }
      ],
      loading: true,
      search: null
    };
  },
  methods: {
    getExchangeRate() {
      let _self = this;
      const cors = "https://cors-anywhere.herokuapp.com/"; // use cors-anywhere to fetch api data
      const url = "https://tw.rter.info/capi.php?=1568944322585"; // origin api url


      /** fetch api url by cors-anywhere */
      axios.get(`${cors}${url}`).then(
        response => {
            let _self = this;
            let titleList = response.data;

            Object.keys(titleList).forEach(function(key) {
              var sObj = { Title: key, Exrate: "", UTC: "" };
              _self.tableData.push(sObj);
            });

            Object.values(titleList).forEach(function(item, index) {
              _self.tableData[index].Exrate = item.Exrate;
              _self.tableData[index].UTC = item.UTC;
            });

            _self.loading = false;
        },
        error => {}
      );
    }
  },
  mounted() {
    this.getExchangeRate();
  }
};
</script>



