<template>
  <el-row>
      <el-card  style="width:60%;margin:0 auto;"  v-for="(item,index) in cardList.filter(c=>c.IsShow == true)" :key="index" @click.native="cardClick(item)" :shadow="item.showdow" class="box-card" :body-style="{ padding: '10px'}">
        <img v-if="item.router" :src="require(`../assets/CardListPicture/${item.router}.jpg`)" class="image">
        <!-- <img src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png" class="image"> -->
        <div slot="header" class="clearfix">
          <i :class="item.icon"></i>
          <span>{{item.name}}</span>
        </div>
      </el-card>
  </el-row>
</template>

<script>
export default {
  name: "CardList",
  data() {
    return {
      cardList: [
        { name: "專案管理系統", showdow: "hover", router:"PMS",desc:"專案管理系統",icon:"el-icon-document",color:'',IsShow:true},
        { name: "通用匯入系統", showdow: "hover", router:"Import", desc:"通用匯入系統",icon:"el-icon-upload2",color:'',IsShow:false},
        { name: "指向績效衡量工具", showdow: "hover", router:"Point",desc:"指向績效衡量工具",icon:"el-icon-top-right",color:'',IsShow:false},
        { name: "ORTool", showdow: "hover", router:"ORTool",desc:"ORTool",icon:"el-icon-finished",color:'',IsShow:false},
        { name: "API", showdow: "hover", router:"API", desc:"API",icon:"el-icon-connection",color:'',IsShow:false},
        { name: "Sample", showdow: "hover", router:"Sample", desc:"Sample",icon:"el-icon-connection",color:'',IsShow:true},
        { name: "即時匯率", showdow: "hover", router:"ExchangeRate", desc:"ExchangeRate",icon:"el-icon-coin",color:'',IsShow:false},
        { name: "DiscordBOT", showdow: "hover", router:"DiscordBOT", desc:"DiscordBOT",icon:"el-icon-platform-eleme",color:'',IsShow:false},
        { name: "SafePeriod", showdow: "hover", router:"SafePeriod", desc:"SafePeriod",icon:"el-icon-date",color:'',IsShow:false},
      ]
    };
  },
  methods:{
     cardClick(item){
      this.$router.push(item.router).catch(err => {});
    },
  },
  mounted(){

  },
  components:{

  }
};
</script>

<style scoped>
  .el-card{
    cursor: pointer;
  }
   .image {
    width: 100%;
    display: block;
  }

</style>



