<template>
  <!-- <div id="app">
    <router-view/>
  </div> -->
  <el-row>
      <vue-particles
          color="#409EFF"
          :particleOpacity="0.7"
          :particlesNumber="80"
          shapeType="circle"
          :particleSize="4"
          linesColor="#409EFF"
          :linesWidth="1"
          :lineLinked="true"
          :lineOpacity="0.4"
          :linesDistance="150"
          :moveSpeed="2"
          :hoverEffect="true"
          hoverMode="grab"
          :clickEffect="true"
          clickMode="push">
     </vue-particles>
     <el-header class="header">
          <Header></Header>
      </el-header>
      <el-container>
          <el-main id="elmain">
               <router-view></router-view>
          </el-main>
      </el-container>
  </el-row>
</template>

<script>
import MainPage from './components/MainPage'
import Aside from './components/Aside'
import Header from './components/Header'
import CardList from './components/CardList'
export default {
  name: 'Layout',
  data(){
    return{
      isCollapse:false
    }
  },
  components: {
     MainPage,
     Aside,
     Header,
     CardList
  },
  mounted(){
    this.$router.push('CardList').catch(err => {});
  }
}
</script>

cool-background.png

<style>
.header {
  background-color: #409EFF;
  color: #fff;
  line-height: 60px;
}
#particles-js { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }
body{
  background-color: black
}
</style>



